  background-position: center;
  background-size: 16px 32px;
  border-radius: 50%;
  cursor: pointer;
}
.reviews__swiper-button--prev {
  left: calc((100% - 560px) / 2 - 76px);
  background-image: url("../img/svg/swiper.svg");
  transform: translate(0, -50%);
}
