  background-image: url("../img/svg/swiper-hover-focus.svg");
  background-size: 56px 56px;
  outline: none;
}
.reviews__swiper-button--prev:active {
  background-image: url("../img/svg/swiper-active.svg");
  background-size: 56px 56px;
}
.reviews__swiper-button--next {
  right: calc((100% - 560px) / 2 - 76px);
  background-image: url("../img/svg/swiper-right.svg");
  transform: translate(0, -50%);
}
.reviews__swiper-button--next:hover, .reviews__swiper-button--next:focus-visible {
  background-image: url("../img/svg/swiper-right-hover-focus.svg");
  background-size: 56px 56px;
  outline: none;
}
.reviews__swiper-button--next:active {
  background-image: url("../img/svg/swiper-right-active.svg");
  background-size: 56px 56px;
}
@media (max-width: 1199px) {
  .reviews__swiper-button {
    top: 295px;
  }
  .reviews__swiper-button--prev {
    left: calc((100% - 560px) / 2 - 78px);
  }
  .reviews__swiper-button--next {
    right: calc((100% - 560px) / 2 - 78px);
  }
}
@media (max-width: 767px) {
